.navbarF{
margin-top: 10px;
width: 33.333333%;
}
.navbarFlogo img{
 cursor: pointer;
 object-fit: cover;
 display: block;
}
 .navbarM{
  width: 33.333333%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  
}
.navbarSer{
  position: relative;
}
.navbarS{
  background: rgba(235, 225, 225, 0.568);
  border: 1px solid green;
  padding: 7px 25px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
 text-align: center;
}
.searchIcon{
  position: absolute;
  top: 8px;
  left: 70px;
  color: silver;
} 
.navbarL{
  width: 33.33333%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.navbarL li{
  display: inline-block;
  margin: 0 10px;
  list-style-type: none;
  cursor: pointer;
}
.navbarL li:hover{ 
 color: red;
 border-bottom: 3px solid black;
}
.navI{
  font-size: 25px;
  color: rgb(71, 63, 63);
}
.navI:hover{
  color: red;
} 
