.App {
  
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.storis{
  display: flex;
  background: white;
  height: 150px;
  
  border-radius: 3px;
  border: 2px solid black;
  overflow-x: scroll;
  margin: 70px 0;
}
.stories__info{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 6px;
  cursor: pointer;
}
.stories__img{
  width: 115px;
  height: 115px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid red;
  padding: 5px;
  
}
.stories__img span{
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
}
.stories__img span img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.stories__name{
  font-size: 11px;
  margin-top: 5px;
}
.create{
 
  height: 200px;
  margin: 15px 0px;
  background: white;
  padding: 10px;
  border: 1px solid black;
  border-radius: 3px;

}
.create__input{
  width: 100%;
  height: 90px;
  border: 1px solid #dfcbcb;
  outline: none;
  border-radius: 3px;
  background: rgb(219, 212, 197);
}
.createinput{
  width: 100%;
  height: 100%;
}
.create__second{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}
.file{
  display: none;
}
.camera{
  color: red;
  height: 50px;
  width: 80px;
  align-items: flex-start;
}
.create__second_a{
  flex: 1 1;
}
.create__second_b{
  flex: 1 1;
  text-align: right;
}
.btn-submit{
  background: red;
  color: white;
  height: 50px;
  width: 80px;
  border: none;
  padding: 6px 10px;
  border-radius: 5px;
}
.post{
 
  height: auto;
  
  margin: 30px 0px;
  border: 1px solid rgba(99, 81, 81, 0.555);
}
.postF{
  height: 50px;
  width: 100%;
  background-color: white;
  display: flex;
}
.postFF{
  height: 50px;
  width: 50%;
  display: flex;
}
.postFF a{
 margin: 15px;
}
.postimg{
  width: 30px;
  height: 30px;
 padding: 10px;
}
.postimg img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.postf{
  height: 50px;
  width: 50%;
  display: grid;
  place-items: end;
  align-items: center;
  margin-right: 20px;
  font-weight: bold;
}
.postM{
  height: 100vh;
  width: 100%;
  margin-top: 10px;
}
.postM img{
  height: 100%;
  width: 100%;
}
.postL{
  height: 150px;
  
  margin: 10px;
}
.pol1{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30%;
  width: 100%;
}
.pi{
  height: 25px;
  width: 25px;
  margin: 15px;
  color: black;
  background: white;
}
.pol2{
 
  display: flex;
  justify-content: space-between;
  margin: 10px;
 
}
.pol2 form{
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
}
.pol2 input{
  border: none;
}
.pol2 button{
  border: none;
  background: white;
  color: blue;
}
.pii{
  height: 50px;
  width: 85%;
  margin-right: 5px;
  display: flex;
}
.piii{
  height: 50px;
  width: 50px;
  margin-left: 5px;
  
}
.navl{
  width: 350px;
  position: fixed;
  margin-top: 100px;
  padding: 0 30px;
}
.navl1{
  height: 70px;
  width: 50%;
  display: flex;
}
.navl span{
  height: 20px;
  width: auto;
  margin-top: 10px;
 text-align: center;
 padding: 10px;
}
.navl1 a{
 margin-top: 15px;
}
.navlimg{
  width: 50px;
  height: 50px;
 padding: 10px;
}
.navl1 img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.navl2{
  display: flex;
  justify-content: space-between;
  margin: 0 15px;
}
.ppp{
  height: 50px;
  width: auto;
  display: flex;
  align-items: center;
  margin: 0 15px;
  justify-content: space-between;
}
.ppp a{
  color: blue;
}
.pppp{
  height: 40px;
  width: 40px;
  border-radius: 50%;
}
.pppp img{
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
.margin{
  display: flex;
  margin: 0 200px;
}
.navright{
  width: 60%;
}
.navleft{
  width: 40%;
}
a{
  cursor: pointer;
}
img{
  cursor: pointer;
}
